var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"site-wrapper-reveal"},[_vm._ssrNode("<div class=\"hero-area bg-overlay-black hero-style-01 hindu-hero-bg\"><div class=\"container\"><div class=\"row\"><div class=\"col-lg-12\"><div class=\"hero-content text-center\"><h1 class=\"text-white\">Nuggehalli Sri Lakshmi Narasimha Swamy Seva Samith</h1></div></div></div></div></div> <div class=\"tai-about-area section-space--ptb_120\"><div class=\"container-fluid pl-0 pr-0\"><div class=\"row no-gutters align-items-center\"><div class=\"col-lg-6\"><div class=\"about-tai-image text-center\"><img"+(_vm._ssrAttr("src",require("assets/images/banners/geo-location.png")))+" alt class=\"img-fluid\"></div></div> <div class=\"col-lg-6\"><div class=\"about-tai-content col-06__right tablet-mt__30 small-mt__30\"><div class=\"section-title-wrap\"><h3 class=\"section-title left-style\">Importance of the Place</h3></div> <p>\n              Situated in this quiet &amp; quaint village are five to six 11th century temples, of\n              which two are a treasure trove of architecture. The aura of the temples are felt by\n              visiting these temples. However due to neglect of these grand edifices by the\n              Government / Archaeological Survey of India (ASI), people having a lineage to\n              Nuggehalli came together more than 40yrs ago to form a society under the name\n              Nuggehalli Sri Lakshmi Narasimha Swamy Seva Samithi for the upkeep of the temples &amp;\n              to ensure the day to day ‘pooja’s’ are performed to the almighty as per the\n              ‘shastras’.\n            </p> <br> <p>\n              The historical temples of Sri Lakshmi Narasimha &amp; Sadashiva at Nuggehalli belonging\n              to 11th century Hoysala period architecture is a must visit place of\n              tourist/cultural importance. The sculptures in the temple tease the mind with a\n              beauty that would linger in one’s mind for ages. These wonderful temples come under\n              the purview of the Archaeological Survey of India (ASI) and they have classified\n              these as the “Living Temple – Monument”, (which are very few in number) wherein the\n              pooja to the Almighty is being performed uninterrupted from day one. This\n              classification is given to about 50 odd temples in India. Karnataka is indeed\n              fortunate to have quite a few of these temples which have a Great &amp; Hoary past.\n            </p></div></div></div></div></div> <div class=\"donation-area section-space--pb_120\"><div class=\"container\"><div class=\"row\"><div class=\"col-lg-12\"><div class=\"section-title-wrap text-center\"><h3 class=\"section-title center-style\">Our Temple</h3></div></div></div> <div class=\"row\"><div class=\"col-lg-12\"><div class=\"single-donation-wrap row align-items-center\"><div class=\"col-lg-5\"><div class=\"donation-image\"><img"+(_vm._ssrAttr("src",require("assets/images/donation/hindu-donate-01.png")))+" alt=\"Donation Image\" class=\"img-fluid\"></div></div> <div class=\"col-lg-7\"><div class=\"donation-content ml-lg-5\"><div class=\"content-title\"><h4 class=\"mb-15\">Geographical Position</h4> <p>\n                    Nuggehalli is a village &amp; Hobli HQ in Hassan district of Karnataka. It is\n                    16kms northeast of Channarayapatna &amp; 16kms east of Hiresave on NH 47 &amp; is very\n                    close to Sravanabelgola (28Kms). The deviation to both Nuggehalli &amp;\n                    Sravanabelogola is from Hiresave &amp; one could reach Nuggehalli from Bangalore\n                    within three hrs.\n                  </p></div></div></div></div></div> <div class=\"col-lg-12\"><div class=\"single-donation-wrap row align-items-center\"><div class=\"col-lg-5\"><div class=\"donation-image\"><img"+(_vm._ssrAttr("src",require("assets/images/donation/hindu-donate-02.png")))+" alt=\"Donation Image\" class=\"img-fluid\"></div></div> <div class=\"col-lg-7\"><div class=\"donation-content ml-lg-5\"><div class=\"content-title\"><h4 class=\"mb-15\">History of the Town</h4> <p>\n                    Nuggehalli used to be a flourishing town as seen in the inscription Ch.238. &amp;\n                    an ancient place near which the Chola king is said to have constructed a\n                    temple of ‘Jayagondeshwara’ &amp; to which the Hoysala king Vishnuvardhana gave\n                    some grants in 1121 A.D. A number of temples dedicated to ‘Vishnu &amp; his\n                    incarnation’ were constructed during the reign of Hoysala King Vishnuvardhana.\n                    The Jain King came under the profound influence of the Srivaishnava saint Sri\n                    Ramanujacharya. (It was during this time an impetus to Hinduism over the then\n                    religion of Jainism – as signified by the sculptures &amp; basadies of\n                    Sravanabelgola gained strength) &amp; had several such temples constructed in his\n                    kingdom.\n                  </p></div></div></div></div></div> <div class=\"col-lg-12\"><div class=\"single-donation-wrap row align-items-center\"><div class=\"col-lg-5\"><div class=\"donation-image\"><img"+(_vm._ssrAttr("src",require("assets/images/donation/hindu-donate-03.png")))+" alt=\"Donation Image\" class=\"img-fluid\"></div></div> <div class=\"col-lg-7\"><div class=\"donation-content ml-lg-5\"><div class=\"content-title\"><h4 class=\"mb-15\">History of Temple</h4> <p>\n                    The original Hoysala temple appears to have had a Mahadvara or upparige of\n                    soapstone supported by plain round pillars. In front of the Mahadvara stood\n                    the two fine elephants, which are now in front of the Hajara (inner\n                    Courtyard). The elephants are well carved and an equestrian is cantering on\n                    either side of the trunk of each. Ahead of the Mahadwara (in the west), a part\n                    of the courtyard stands a platform about four feet high on which the temple is\n                    raised.\n                  </p></div></div></div></div></div></div></div></div> <div class=\"hindu-video-area\"><div class=\"container-fluid container-fluid--cp-100\"><div class=\"hindu-video-bg border-radius-5\"><div class=\"row\"><div class=\"col-lg-12 ml-auto mr-auto\"><div class=\"video-content-wrap text-center\"><div class=\"icon\"><div class=\"embed-responsive embed-responsive-16by9\"><iframe src=\"https://www.youtube.com/embed/GzJ1m3SY47I\" allowfullscreen=\"allowfullscreen\" class=\"embed-responsive-item\"></iframe></div></div></div></div></div></div></div></div>")])
}
